<template>
  <v-main>
    <div class="mb-4 mx-4 mt-n6" style="font-size: 120%">
      加入率 {{ this.NumOfObjective }} / {{ this.CharaLength }}
    </div>
    <v-layout row class="mx-3" style="flex-wrap: wrap">
      <v-card
        v-for="chara in charaList"
        v-bind:key="chara['m_CharaID']"
        class="ma-0 pa-0 d-flex flex-wrap"
        style="flex-wrap: wrap"
      >
        <v-card
          style="line-height: 0px; flex-wrap: wrap"
          width="50px"
          height="50px"
          @click="switchObjData(chara['m_CharaID'])"
        >
          <img
            crossOrigin="anonymous"
            :id="'Img' + chara['m_CharaID']"
            :src="'./mergedcharaicon/charaicon_' + chara['m_CharaID'] + '.png'"
            aspect-ratio="1"
            width="50px"
            decoding="async"
            style="position: absolute; z-index: 2"
          />
          <img
            crossOrigin="anonymous"
            :id="'Img' + chara['m_CharaID']"
            src="../assets/loading.gif"
            aspect-ratio="1"
            width="50px"
            decoding="async"
            style="position: absolute"
          />
          <img
            :id="'Obj' + chara['m_CharaID']"
            crossOrigin="anonymous"
            src="../assets/Owned.png"
            aspect-ratio="1"
            width="50px"
            :style="switchStyle(chara['m_CharaID'])"
          />
        </v-card>
      </v-card>
    </v-layout>
    <span style="display: none">
      <canvas id="canvas" width="1000"> </canvas>
    </span>
    <v-snackbar color="info" v-model="isMakingImage" timeout="10000">
      画像生成中…
    </v-snackbar>
  </v-main>
</template>

<script>
export default {
  name: "home",

  data: () => ({
    isLoaded: false,
    isMakingImage: false,
  }),

  computed: {
    NumOfObjective() {
      return Object.keys(this.getObjChara()).filter(
        (x) => this.getObjChara()[x]
      ).length;
    },
    CharaLength() {
      return this.charaList.length;
    },
  },

  methods: {
    // async getImage(id) {
    // //https://card-assets.kirafan.cn
    // const ImageURL = `../mergedcharaicon/charaicon_${id}.png`;
    // if (this.storage.isSupported) {
    // //   let blob = await this.storage.getItem(id);
    // //   if (blob === undefined) {
    // //     let download_img = await fetch(ImageURL);
    // //     blob = await download_img.blob();
    // //     this.storage.setItem(id, blob);
    // //   }
    // //   return await URL.createObjectURL(blob);
    // // } else {
    //   return ImageURL;
    // }
    // },

    async outputImage() {
      const NumOfObjective = () =>
        Object.keys(this.getObjChara()).filter((x) => this.getObjChara()[x])
          .length;

      const CharaLength = () => this.charaList.length;

      this.isMakingImage = true;
      const canvas = document.getElementById("canvas");
      const ctx = canvas.getContext("2d");
      const header = 60;
      canvas.height = Math.ceil(this.charaList.length / 10) * 100 + header;

      ctx.fillStyle = "rgb(255,255,255)";
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      ctx.font = "48px Arial";
      ctx.fillStyle = "rgb(0,0,0)";

      ctx.fillText(`加入率：${NumOfObjective()} / ${CharaLength()}`, 5, 50);

      for (let i = 0; i < this.charaList.length; i++) {
        let charaId = this.charaList[i]["m_CharaID"];
        let image_elm = document.getElementById("Img" + charaId);
        const draw = (elm) =>
          ctx.drawImage(
            elm,
            100 * (i % 10),
            100 * Math.floor(i / 10) + header,
            100,
            100
          );

        draw(image_elm);
        if (this.getObjChara(charaId)) {
          let obj_elm = document.getElementById("Obj" + charaId);
          draw(obj_elm);
        }
      }

      let vm = this;
      canvas.toBlob(callback);
      async function callback(x) {
        downloadFromUrlAutomatically(
          await URL.createObjectURL(x),
          `${dateFunction()}.png`
        );
        vm.isMakingImage = false;
      }

      function dateFunction() {
        //日付オブジェクトを作成する
        var dd = new Date();
        //「年」を取得する
        var YYYY = dd.getFullYear();
        //「月」を取得する
        var MM = dd.getMonth() + 1;
        //「日」を取得する
        var DD = dd.getDate();
        //段落タグ("date011")の中身を置き換える
        return "" + YYYY + MM + DD;
      }

      function downloadFromUrlAutomatically(url, fileName) {
        var xhr = new XMLHttpRequest();
        xhr.open("GET", url, true);
        xhr.responseType = "blob";
        xhr.onload = function () {
          if (this.status == 200) {
            var urlUtil = window.URL || window.webkitURL;
            var imgUrl = urlUtil.createObjectURL(this.response);
            var link = document.createElement("a");
            link.href = imgUrl;
            link.download = fileName;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
          }
        };
        xhr.send();
      }
    },

    switchStyle(id) {
      if (this.getObjChara(id)) {
        return "position:relative;z-index: 5;";
      } else {
        return "display:none;";
      }
    },

    switchObjData(id) {
      let objChara = !this.getObjChara(id);
      this.setObjChara(id, objChara);
      if (this.storage.isSupported)
        this.storage.setItem("ObjectiveChara", this.getObjChara());
    },
  },

  props: {
    charaList: {
      type: Array,
    },
    storage: {
      type: Object,
    },
    setObjChara: {
      type: Function,
    },
    getObjChara: {
      type: Function,
    },
  },

  mounted() {
    this.$emit("dl-image", this.outputImage);
  },
};
</script>
